import React, { useState } from 'react';

import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'
import FormGroup from 'react-bootstrap/FormGroup';
import Alert from 'react-bootstrap/Alert';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';

export default function CreaNota() {

    const [success, setSuccess] = useState(false);
    const [error, setError] = useState(false);
    const [loader, setLoad] = useState(false);

    var correus = [
        {nom: "Tots els monitors i monitores", correu: "notetesstpere@gmail.com", id: 0},
        {nom: "Aina", correu: "ainaamora21@gmail.com", id: 1},
        {nom: "Alba", correu: "carrilloamat@gmail.com", id: 2},
        {nom: "Ari", correu: "ariadnablanes12@gmail.com", id: 3},
        {nom: "Carla", correu: "carlafluri@hotmail.com", id: 4},
        {nom: "Clara", correu: "clara@garrit.cat", id: 5},
        {nom: "Eli", correu: "2000elicm@gmail.com", id: 6},
        {nom: "Enric", correu: "Enrixcid@gmail.com", id: 7},
        {nom: "Eric", correu: "ericrodatrifol@gmail.com", id: 8},
        {nom: "Gigi", correu: "georginaverdaguer97@gmail.com", id:9},
        {nom: "Isi", correu: "i.puigferrat@gmail.com", id: 10},
        {nom: "JP", correu: "joanpaucm28@gmail.com", id: 11},
        {nom: "Laia", correu: "laiadf777@gmail.com", id: 12},
        {nom: "Laura", correu: "lauralardies15@gmail.com", id: 13},
        {nom: "Marc", correu: "maliguer@alumni.unav.es", id: 14},
        {nom: "Maria", correu: "mariamoller10@gmail.com", id: 15},
        {nom: "Mimi", correu: "mimigomez4@gmail.com", id: 16},
        {nom: "Nati", correu: "nmolleribas@gmail.com", id: 17},
        {nom: "Oriol Mora", correu: "oriol_mora@hotmail.com", id: 18},
        {nom: "Oriol Serra", correu: "uri.serra26@gmail.com", id: 19},
        {nom: "Santi", correu: "santi.claret@outlook.com", id: 20},
        {nom: "Tere", correu: "teresa.puigferrat@gmail.com", id: 21},
        {nom: "Zalaya", correu: "mynameisvzalaya@gmail.com", id: 22}
    ]

    const listMonis = correus.map((moni) => 
        <option value={moni["id"]} key={moni["id"]} >{ moni["nom"] }</option>
    );

    function sendEmail(e) {

        setLoad(true);

        var nomInfant = document.getElementById('NomInfant');
        var nomMoni = document.getElementById('monisSelect');
        var textNota = document.getElementById('noteta');
        var respondre = document.getElementById('replyEmail');


        var variables = {
            from_name: nomInfant.value,
            to_name: correus[nomMoni.value]["nom"],
            message_html: textNota.value,
            send_to: correus[nomMoni.value]["correu"],
            reply_to: respondre.value
        }

        console.log(variables)

        e.preventDefault();

        window.emailjs.send('gmail', 'template_NzzwThX7', variables)
            .then((result) => {
                console.log("Missatge enviat!");
                setLoad(false);
                setSuccess(true);
            }).catch( (error) => {
                console.log("Missatge no enviat!");
                setLoad(false);
                setError(true);
            });
    }
    
    return (
        <div className="MainForm" >

            <Form id="form" onSubmit={sendEmail}>

                {/* Demanem el nom de l'nfant */}
                <FormGroup controlId="NomInfant" >
                    <Form.Label>Escriu aquí el teu nom</Form.Label>
                    <Form.Control required type="text" placeholder="El teu nom" />
                </FormGroup>
                
                {/* Demanem el moni a qui se li vol enviar la nota */}
                <Form.Group controlId="monisSelect">
                    <Form.Label>A quin monitor/a li vols enviar la noteta?</Form.Label>
                    <Form.Control as="select" defaultChecked="Tots els monitors i monitores" >
                        {listMonis}
                    </Form.Control>
                </Form.Group>

                {/* Demanem el text de la nota */}
                <Form.Group required controlId="noteta">
                    <Form.Label>Escriu aquí la noteta</Form.Label>
                    <Form.Control as="textarea" rows="5" />
                </Form.Group>


                {/* Correu per respondre */}
                <Form.Group controlId="replyEmail">
                    <Form.Label>Si vols que el monitor/a et respongui, escriu el teu correu aquí!</Form.Label>
                    <Form.Control type="email" placeholder="Escriu el teu correu aquí" />
                    <Form.Text className="text-muted">
                        Assegura't que l'escrius bé!
                    </Form.Text>
                </Form.Group>

                {/* Alerta per si funciona */}
                <Alert show={success} variant="success" >
                    <Alert.Heading>Missatge enviat correctament!</Alert.Heading>
                    <p>
                        Ara ja pots esborrar la noteta o algú altre la podrà llegir!
                    </p>
                    <hr />
                    <div className="d-flex justify-content-end">
                        <Button onClick={() => window.location.reload(false)} variant="outline-success">
                            Esborra la noteta!
                        </Button>
                    </div>
                </Alert>

                {/* Alerta per si NO funciona */}
                <Alert show={error} variant="danger" >
                    <Alert.Heading>Hi ha hagut un error a l'enviar la nota!</Alert.Heading>
                    <p>
                        Prova a refrescar la pàgina i tornar a escriure la nota
                    </p>
                    <hr />
                    <div className="d-flex justify-content-end">
                        <Button onClick={() => window.location.reload(false)} variant="outline-danger">
                            Refresca la pàgina
                        </Button>
                    </div>
                </Alert>

                {/* Submit button */}
                <Container>
                    <Row className="loadButton">
                        <Button variant="primary" type="submit" id="enviar" >
                            <span className="buttonText">Enviar noteta</span>
                            { loader ? <div className="loaderContainer"> <div className="loader"></div> </div> : null }
                        </Button>
                    </Row>
                </Container>

            </Form>
        </div>
    );
}